html,
body,
#root {
  background-color: #f0e5d8;
  width: 100%;
  height: 100%;
}

.App {
  text-align: center;
  color: rgb(196, 190, 190);
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  width: 100%;
}

.project-list {
  height: 100%;
  width: 306px;
  display: flex;
  overflow: hidden;
  border-radius: 24px;
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.Title {
  padding-left: 20px;
}
.App-header {
  display: flex;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  color: white;
  background-color: #2d2d30;
  width: 100%;
  height: 8%;
}

.project-carousel {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  max-width: 500px;
  width: 100%;
}

.project-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.chooseIndex {
  display: flex;
  color: #2d2d30;
}
button {
  border: none;
  background-color: rgb(0, 0, 0, 0);
  font-size: 30px;
  color: #2d2d30;
  cursor: pointer;
  width: 15px;
  height: 50%;
  padding: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.project {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 520px;
  min-width: 300px;
  background-color: #2d2d30;
  border-radius: 24px;
  border: 3px solid #a5c9ca;
  transition: translate 200ms ease-in-out;
}

.description {
  text-align: left;
  height: 350px;
  /* border: 2px solid red; */
  margin: 0;
  font-size: 15px;
  width: 85%;
}

.btns {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10%;
}

a {
  padding: 4px;
  margin: 0 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1.5rem;
  width: 3.5rem;
  border-radius: 8px;
  text-decoration: none;
  background-color: #2e4052;
  color: #f0e5d8;
  border: 1.5px solid #a5c9ca;
}

.Panda {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: left;
  color: #2d2d30;
  height: 250px;
  width: 250px;
  font-size: 8px;
  margin: 20px;
  font-weight: bold;
  /* background-color: white; */
  /* box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.2), 0 0px 8px 0 rgba(0, 0, 0, 0.2); */
  border-radius: 50%;
}

h1 {
  margin: 0;
  margin-top: 10px;
  font-size: 22px;
  color: #d9bf77;
}

.footer {
  height: 8%;
  background-color: #2d2d30;
  width: 100%;
}

